html {
    margin: 0;
    padding: 0;
}
body {
    font-family: 'Handlee', cursive;
    font-size: 13pt;
    background-color: #efefef;
    margin: 0;
    /* width: 100vw;
    max-width: 100vw; */
}
h1 {
    font-size: 15pt;
    color: #20bcd5;
    text-align: center;
    padding: 18px 0 18px 0;
    margin: 0 0 10px 0;
}
h1 span {
    border: 4px dashed #20bcd5;
    padding: 10px;
}
p {
    padding: 0;
    margin: 0;
}
.img-circle {
    size: 100%;
    border: 1px solid rgb(91, 150, 218);
    border-radius: 20%;
}
.section {
    background-color: #fff;
    padding: 15px;
    margin-bottom: 10px;
    border-radius: 10px;
}
#header {
    background-image: url("https://www.sololearn.com/Uploads/header.jpg");
    background-size: cover;
}
#header img {
    display: block;
    width: 80px;
    height: 80px;
    margin: auto;
}
#header p {
    font-size: 25pt;
    color: #3b464c;
    padding-top: 5px;
    margin: 0;
    font-weight: bold;
    text-align: center;
}
.quote {
    font-size: 12pt;
    text-align: right;
    margin-top: 10px;
}
table {
    width: 100%;
    font-size: 11pt;
}
table, th, td {
    border: 2px solid #cecece;
    border-collapse: collapse;
    text-align: center;
    table-layout: fixed;
}
.selected {
    background-color: #f36f48;
    font-weight: bold;
    color: white;
}
li {
    margin-bottom: 15px;
    font-weight: bold;
}
progress {
    width: 70%;
    height: 20px;
    color: #3fb6b2;
    background: #efefef;
}
progress::-webkit-progress-bar {
    background: #efefef;
}
progress::-webkit-progress-value {
    background: #3fb6b2;
} 
progress::-moz-progress-bar {
    color: #3fb6b2;
    background: #efefef;
}
iframe, audio {
    display: block;
    margin: 0 auto;
    border: 3px solid #3fb6b2;
}
hr {
    border: 0;
    height: 1px;
    background: #f36f48;
}
form {
    text-align: center;
    margin-top: 0;
}
.submit {
    background-color: #3fb6b2;
    padding: 12px 45px;
    border-radius: 5px;
    cursor: pointer;
    color: #ffffff;
    border: none;
    outline: none;
    margin: 0;
    font-weight: bold;
    margin-bottom: 10px;
    font-size: 11pt;
    width: 70%;
}
.submit:hover {
    background-color: #43a09d;
}
textarea {
    height: 100px;
}
input, textarea {
    margin-bottom: 30px;
    font-size: 11pt;
    padding: 15px 10px 10px;
    border: 1px solid #cecece;
    background-color: #efefef;
    color: #787575;
    border-radius: 5px;
    width: 100%;
    outline: none;
    margin-right: 15px;
}
.face {
    transform: scale(0.4);
    margin: 0 auto;
    display: block;
    margin-top: -35px;
    margin-bottom: -25px;
}
#contacts img {
    height: 40px;
    width: 40px;
    margin-left: 7px;
    margin-right: 7px;
}
#contacts a {
    text-decoration: none;
}
#contacts img:hover {
    opacity: 0.8;
}
#contacts {
    text-align: center;
}
.contact form > div {
    width: 70%;
    margin: 0 auto;
}
.copyright {
    font-size: 8pt;
    text-align: right;
    padding-bottom: 10px;
    color: grey;
}

/* Add a black background color to the top navigation */
.navbar {
    top: 0;
    padding: 2px;
    /* border: 1px solid #4CAF50; */
    position: -webkit-sticky;
    position: sticky;
    background-color: #135c6e;
    overflow: hidden;

    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    z-index: 1;
}

/* Style the links inside the navigation bar */
.navbar .navLinks {

    float: right;
    color: #f2f2f2;
    text-align: right;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
    cursor: pointer;

}

/* Change the color of links on hover */
.navbar .navLinks:hover {
background-color: #ddd;
color: black;
}

/* Add a color to the active/current link */
.navbar .navLinks.active {
background-color: #04AA6D;
color: white;
}
.flex-container {
display: flex;
background-color: white;

/* width: 100%;
flex-wrap: wrap; */
padding: 5px 20px;
justify-content: space-evenly;
}

.flex-container > div {
background-color: #c6edee;
margin: 10px;
padding: 20px;
font-size: 30px;
cursor: pointer;
}
.container {

box-shadow: 0 4px 8px 0 rgba(43, 153, 180, 0.2);
transition: 0.3s;
width: 70%;
}
.container p{
    align-content: center;
font-size: medium;
}
.container h4{
text-align: center;
/* font-size: medium; */
}

.container:hover {
box-shadow: 0 8px 16px 0 #1e1234;
}
.skills{
text-align: left;
}
.padding10{
padding: 10px;
}
.scrollTopBuffer{
    scroll-margin-top: 4rem;
}
.pos-rel{
    position: relative;
}
.pos-abs{
    position: absolute;
}
.error-msg{
    color: red;
    position: absolute;
    top: 40px;
}


/* Animation */
.react-reveal {
    opacity: 0;
}
@-webkit-keyframes fadeInUp {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(0,80px,0);
        transform: translate3d(0,80px,0)
    }

    to {
        opacity: 1;
        -webkit-transform: translateZ(0);
        transform: translateZ(0)
    }
}

@keyframes fadeInUp {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(0,80px,0);
        transform: translate3d(0,80px,0)
    }

    to {
        opacity: 1;
        -webkit-transform: translateZ(0);
        transform: translateZ(0)
    }
}

@-webkit-keyframes fadeInLeft {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(50px,0,0);
        transform: translate3d(50px,0,0)
    }

    to {
        opacity: 1;
        -webkit-transform: translateZ(0);
        transform: translateZ(0)
    }
}

@keyframes fadeInLeft {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(50px,0,0);
        transform: translate3d(50px,80,0)
    }

    to {
        opacity: 1;
        -webkit-transform: translateZ(0);
        transform: translateZ(0)
    }
}

@-webkit-keyframes fadeInRight {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(-50px,0,0);
        transform: translate3d(-50px,0,0)
    }

    to {
        opacity: 1;
        -webkit-transform: translateZ(0);
        transform: translateZ(0)
    }
}

@keyframes fadeInRight {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(-50px,0,0);
        transform: translate3d(-50px,80,0)
    }

    to {
        opacity: 1;
        -webkit-transform: translateZ(0);
        transform: translateZ(0)
    }
}

.fade-in-up, .fade-in-up-width100 {
    -webkit-animation-name: fadeInUp;
    animation-name: fadeInUp;
}
.fade-in-up-width100 {
    width: 100%
}

.fade-in-left {
    -webkit-animation-name: fadeInLeft;
    animation-name: fadeInLeft
}

.fade-in-right {
    -webkit-animation-name: fadeInRight;
    animation-name: fadeInRight
}
.react-reveal.active{
    animation-duration: 1000ms;
    animation-delay: 0ms;
    animation-iteration-count: 1;
    opacity: 1;
}













@media only screen and (max-width: 768px) {
    .flex-container{
        flex-wrap: wrap;
    }
    .container{
        width: 100%;
    }
    .navbar{
        padding: 0px 5px;
        justify-content: space-between;
    }
    .navbar a{
        font-size: 1rem;
        padding: 14px 5px;
    }
}